<template>
    <div class="tile is-parent is-vertical is-6">
        <b-field class="tile" horizontal label="">
            <b-input name="name" :value="name" @input="REGISTER_NAME" placeholder="" icon="user" icon-pack="fa" expanded></b-input>
        </b-field>
        <b-field class="tile" horizontal label="">
            <b-input name="company" :value="company" @input="REGISTER_COMPANY" icon="building" icon-pack="fa" expanded></b-input>
        </b-field>
        <b-field class="tile" horizontal label="">
            <b-input name="job" :value="job" @input="REGISTER_JOB" placeholder="" icon="briefcase" icon-pack="fa" expanded></b-input>
        </b-field>
    </div>
</template>

<script>
import { REGISTER_NAME, REGISTER_COMPANY, REGISTER_JOB } from '../store/mutation-types'
import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('profile')
export default {
    computed: {
        ...mapState({
            name: state => state.name,
            company: state => state.company,
            job: state => state.job
        }),
    },
    methods: {
        ...mapActions([
            REGISTER_NAME,
            REGISTER_COMPANY,
            REGISTER_JOB
        ])
    }
}
</script>